.button {
    padding: 10px 10px;  
    flex-wrap: wrap;
    font-size: 24px;
    text-align: center;
    cursor: pointer;
    outline: none;
    color: #fff;

    flex: 1;

    border: none;
    border-radius: 15px;
}
  
.button:hover {
  background-color: rgb(172, 36, 36);
}
  
.button:active {
  background-color:rgb(172, 36, 36);
  box-shadow: 0 5px rgb(119, 26, 26);
  transform: translateY(4px);
}

.selected {
  background-color:rgb(172, 36, 36);
  box-shadow: 0 5px rgb(119, 26, 26);
  transform: translateY(4px);
}

.unselected {
  background-color: rgba(172, 36, 36, 0.247);
  box-shadow: 0 9px rgba(117, 34, 34, 0.678);
}