.tabContainer {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 20px;
}

.blur {
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.5);
}

.tabContentContainer{
    flex: 9;
    height: 100%;
    width: 100%;
    border-radius: 15px;
}

.tabHandler{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}