

.aboutMeContainer{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    align-content: space-between;
}

.tntContainer{
    display: flex;
    flex-direction: column;
    align-items:center;
}

.tntItemsContainer{
    overflow: scroll;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: stretch;

    border-style: solid;
    border-top-width: 1px;
    border-bottom-width: 1px;
    border-left-width: 0px;
    border-right-width: 0px;
    border-color: aliceblue;
}

.tntLangContainer{
    width:100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;

    padding: 10px;
    margin: 5px;

    border-style:inset;
    border-radius: 15px;
    border-color: rgb(60, 61, 122);
    background-image: linear-gradient(to bottom right, rgb(0, 1, 56), rgb(42, 43, 87));
    border-width: 5px;
}

.tntTitle{
    text-decoration: underline;
}

.tntToolList{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.tntToolName {
    width: 100%;
    margin: 5px;
}