.main{
    width: 100%;
    height: 100%;

    display: flex;  
    justify-content: center;
    align-items: center;
}

.container{
    width: 100%;
    height: 100%;

    background-color: transparent;

    display: flex;  
    justify-content: center;
    align-items: center;
}

.border{
    border-style:solid;    
    box-shadow: 0 50px rgb(97, 92, 92);
    transform: rotate3d(1, 0, 0, 8deg);
}

.center {
    overflow: hidden;
    display: flex;  
    justify-content: center;
    align-items: center;
}

.name {
    top: 0px;

    min-height: 0px;
    flex-wrap: wrap;
    justify-content: center;
    color: rgb(192, 173, 63);
    text-align: center;
    text-decoration-style: solid;
    font-weight: 650;
    font-size: 23px;
    letter-spacing: 5px;

    border-style: double;
    border-width: 5px;
    padding-right: 8px;
    padding-left: 8px;

    margin: 5px;
}

.grad {
    background-image: linear-gradient(to bottom right, red, yellow);
    border-image: linear-gradient(to bottom right, red, yellow);
}