.pageContent{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;  
}

.pageContent_inner{
    display: flex;
    position: relative;
    left: 5%;
    width: 90%;
    height: 90%;
    
    justify-content: center;
    align-items: center;
    align-content: center;
}

.whiteText {
    color: aliceblue;
    font-weight: bold;
    font-size: large;
}